import styled from "styled-components";
import Card from "./../../components/Card";
import { polo } from "./../../assets/data.js";

// Component

function Home() {
  return (
    <Container>
      {polo.map(({ title, desc, src, link }, id) => (
        <Card key={id} title={title} desc={desc} src={src} url={link}></Card>
      ))}
    </Container>
  );
}

export default Home;

// Style

const Container = styled.section`
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding: 8rem 7.2rem;
  transition: ease 0.3s;
  @media screen and (max-width: 1190px) {
    padding: 0 0.8rem 4rem;
  }
`;
