import { useLayoutEffect, useRef } from "react";
import { Link } from "react-router-dom";
import gsap from "gsap";
import styled from "styled-components";
import colors from "../../utils/colors";
import { FiArrowUpRight } from "react-icons/fi";

// Component

function Card({ title, desc, src, url, home }) {
  const page = useRef();

  useLayoutEffect(() => {
    gsap.fromTo(
      ".page",
      { opacity: "0" },
      {
        opacity: "1",
        delay: 0.2,
        duration: 0.6,
      }
    );
    window.scrollTo(0, 0);
  }, []);

  return (
    <Container ref={page} className="page" rel="noreferrer" home={home}>
      {home ? (
        <Link to={url}>
          <ImageContainer>
            <img src={src} alt={title} />
          </ImageContainer>
          <DescriptionContainer home={home}>
            {!home && <h3>{title}</h3>}
            <p>{desc}</p>
            <Links className="link">
              {title}
              <FiArrowUpRight />
            </Links>
          </DescriptionContainer>
        </Link>
      ) : (
        <a href={url} target="_blank" rel="noreferrer">
          <ImageContainer>
            <img src={src} alt={title} />
          </ImageContainer>
          <DescriptionContainer home={home}>
            {!home && <h3>{title}</h3>}
            <p>{desc}</p>
            <Links className="link">
              {title}
              <FiArrowUpRight />
            </Links>
          </DescriptionContainer>
        </a>
      )}
    </Container>
  );
}

export default Card;

// Style

const Container = styled.div`
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: ${({ home }) => (!home ? "calc(100% / 3)" : "calc(100% / 2)")};
  padding: 0.8rem;
  @media screen and (max-width: 1190px) {
    width: calc(100% / 2);
  }
  @media screen and (max-width: 768px) {
    width: 100%;
  }
`;

const ImageContainer = styled.div`
  display: flex;
  overflow: hidden;

  img {
    display; flex;
    object-fit: cover;
    width: 100%;
    transition: .3s ease;

    ${Container}:hover & {
    transform: scale(1.05)
  }
  }
`;

const DescriptionContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  font-size: 1.6rem;
  transition: ease 0.3s;
  color: ${colors.dark};
  margin-top: ${({ home }) => (home ? "0" : "1.6rem")};
  margin-bottom: 3.2rem;

  h3 {
    font-size: 2.6rem;
    margin-bottom: 1.6rem;
  }

  p {
    font-size: 1.6rem;
    color: ${colors.mediumDark};
  }
  .link {
    color: ${colors.dark};
  }
`;

const Links = styled.a`
  display: flex;
  align-items: center;
  font-size: 1.6rem;
  font-weight: bold;
  margin-top: 1.6rem;

  ${Container}:hover & {
    text-decoration: underline;
  }

  svg {
    width: 1.6rem;
    height: 1.6rem;
    margin-left: 0.2rem;
  }
`;
